                      class="mr-4"
                      format="HH:mm:ss"
                      placeholder=""
                      start="07:00:00"
                      step="00:30:00"
                      end="20:00:00"
                    />
                    <el-time-select
                      v-model="formData.client.nick.workType.offWorkTime"
                      style="width: 140px"
                      :min-time="formData.client.nick.workType.onWorkTime"
                      placeholder=""
                      format="HH:mm:ss"
                      start="07:00:00"
                      step="00:30:00"
                      end="20:00:00"
                    />
                  </div>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <!--            <el-tab-pane label="IP" name="second">-->
            <!--              <el-form label-width="90">-->
            <!--                <el-form-item label="">-->
            <!--                  <el-input-->
            <!--                    v-model="formData.second.hostname"-->
            <!--                    placeholder=""-->
            <!--                    :input-style="-->
            <!--                      formData.client.static ? { color: 'red' } : {}-->
            <!--                    "-->
            <!--                  />-->
            <!--                </el-form-item>-->
            <!--                <el-form-item label="Mac">-->
            <!--                  <el-input-->
            <!--                    v-model="formData.second.mac"-->
            <!--                    placeholder="Mac"-->
            <!--                  />-->
            <!--                </el-form-item>-->
            <!--                <el-form-item label="IP">-->
            <!--                  <el-input-->
            <!--                    v-model="formData.second.ip"-->
            <!--                    placeholder="IP"-->
            <!--                  />-->
            <!--                </el-form-item>-->
            <!--              </el-form>-->
            <!--            </el-tab-pane>-->
            <el-tab-pane label="" name="thrid" v-if="isThridShow()">
              <div>
                <div style="margin-bottom: 10px">
                  <el-date-picker
                    v-model="value3"
                    type="datetime"
                    placeholder="Select date and time"
                    value-format="x"
                  />

                  <el-popconfirm
                    title="?"
                    @confirm="handleAddWorkTime(true)"
                  >
                    <template #reference>
                      <el-button type="primary" style="margin-left: 10px" plain
                        >
                      </el-button>
                    </template>
                  </el-popconfirm>

                  <el-popconfirm
                    title="?"
                    @confirm="handleAddWorkTime(false)"
                  >
                    <template #reference>
                      <el-button type="primary" style="margin-left: 10px" plain
                        >
                      </el-button>
                    </template>
                  </el-popconfirm>

                  <el-button
                    type="warning"
                    style="margin-left: 10px"
                    plain
                    @click="fetchWorkData"
                    >
                  </el-button>

                  <el-button
                    type="warning"
                    style="margin-left: 10px"
                    plain
                    @click="fetchWorkEvent"
                    >
                  </el-button>
                </div>
                <el-table
                  :data="paginatedTableData"
                  border
                  :preserve-expanded-content="false"
                >
                  <el-table-column type="expand">
                    <template #default="props">
                      <div m="4">
                        <el-table
                          :data="props.row.dayDatas"
                          border
                          :preserve-expanded-content="false"
                        >
                          <el-table-column label="" prop="date" sortable />
                          <el-table-column label="" prop="workTime1">
                            <template #default="scope">
                              <el-time-picker
                                v-model="scope.row.workTime1"
                                style="width: 100px"
                                arrow-control
                                value-format="HH:mm:ss"
                              />
                            </template>
                          </el-table-column>
                          <el-table-column label="" prop="workTime2">
                            <template #default="scope">
                              <el-time-picker
                                v-model="scope.row.workTime2"
                                style="width: 100px"
                                arrow-control
                                value-format="HH:mm:ss"
                              />
                            </template>
                          </el-table-column>
                          <el-table-column
                            prop="soverHours"
                            label=""
                            align="center"
                          >
                            <template #default="scope">
                              <el-tag type="primary" size="default"
                                >{{
                                  // formatNanosecondsToHours(scope.row.overHours)
                                  scope.row.soverHours
                                }}
                              </el-tag>
                            </template>
                          </el-table-column>
                          <el-table-column label="" prop="weekday">
                            <template #default="scope">
                              {{ getWeekDay(scope.row.weekday) }}
                            </template>
                          </el-table-column>
                          <el-table-column label="" prop="isWeekDay">
                            <template #default="scope">
                              <el-tag
                                v-if="!scope.row.showSelect"
                                :type="getTagType(scope.row.dayType)"
                                @dblclick="handleShowSelect(scope.row)"
                                >{{ getTagName(scope.row.dayType) }}
                              </el-tag>
                              <el-select
                                v-else
                                v-model="scope.row.dayType"
                                @change="handleSelectChange(scope.row)"
                              >
                                <el-option
                                  v-for="item in options"
                                  :label="item.label"
                                  :key="item.label"
                                  :value="item.value"
                                />
                              </el-select>
                            </template>
                          </el-table-column>
                          <el-table-column
                            label=""
                            max="80"
                            fixed="right"
                            align="center"
                          >
                            <template #default="{ row }">
                              <el-dropdown
                                size="small"
                                split-button
                                type="primary"
                              >
                                <el-popconfirm
                                  title=""
                                  @confirm="handleChangeWorkTime(row)"
                                >
                                  <template #reference> </template>
                                </el-popconfirm>
                                <template #dropdown>
                                  <el-dropdown-menu>
                                    <el-dropdown-item
                                      @click="handleDeleteWorkTime(row)"
                                      >
                                    </el-dropdown-item>
                                  </el-dropdown-menu>
                                </template>
                              </el-dropdown>
                            </template>
                          </el-table-column>
                        </el-table>
                      </div>
                    </template>
                  </el-table-column>
                  <el-table-column prop="month" label=""></el-table-column>
                  <el-table-column
                    prop="stotalOverHours"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="danger" size="large"
                        >{{
                          // formatNanosecondsToHours(scope.row.totalOverHours)
                          scope.row.stotalOverHours
                        }}
                      </el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="sworkDayOverHours"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="warning"
                        >{{
                          // formatNanosecondsToHours(scope.row.workDayOverHours)
                          scope.row.sworkDayOverHours
                        }}
                      </el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="sworkDayAveOverHours"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="primary"
                        >{{
                          // formatNanosecondsToHours(
                          //   scope.row.workDayAveOverHours,
                          // )
                          scope.row.sworkDayAveOverHours
                        }}
                      </el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="dayCount"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="warning">{{ scope.row.dayCount }}</el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="ssaturdayOverHours"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="success"
                        >{{
                          // formatNanosecondsToHours(scope.row.saturdayOverHours)
                          scope.row.ssaturdayOverHours
                        }}
                      </el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="ssaturdayAveOverHours"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="primary"
                        >{{
                          // formatNanosecondsToHours(
                          //   scope.row.saturdayAveOverHours,
                          // )
                          scope.row.ssaturdayAveOverHours
                        }}
                      </el-tag>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="saturdayCount"
                    label=""
                    align="center"
                  >
                    <template #default="scope">
                      <el-tag type="warning"
                        >{{ scope.row.saturdayCount.length }}({{
                          scope.row.weekCount
                        }})
                      </el-tag>
                    </template>
                  </el-table-column>
                </el-table>
                <!--  -->
                <el-pagination
                  style="margin-top: 20px"
                  v-model:page-size="pageSize"
                  v-model:current-page="currentPage"
                  :page-sizes="[10, 20, 50, 100, 1000]"
                  layout="sizes,prev, pager, next"
                  background
                  :size="size"
                  :total="monthData.length"
                  @size-change="handleSizeChange"
                  @current-change="handlePageChange"
                />
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </template>
      <template #footer v-if="formData.showFooter">
        <el-button
          type="danger"
          v-if="formData.hideErrBtn"
          :loading="formData.deleteloading"
          :loading-icon="Eleme"
          @click="handleDeleteStaticIp"
          >{{ formData.deleteloading ? '...' : '' }}
        </el-button>
        <el-button
          type="primary"
          :loading="formData.loading"
          :loading-icon="Eleme"
          @click="handleConfirm"
          >{{ formData.loading ? '...' : '' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { Eleme } from '@element-plus/icons-vue'
import { ref, defineExpose, computed } from 'vue'
import {
  Client,
  DayData,
  MonthData,
  NickEntry,
  WorkType,
} from '../utils/type.ts'
import {
  getWeekDay,
  isMobile,
  showErrorTips,
  showLoading,
  showSucessTips,
  showTips,
} from '../utils/utils.ts'
import { ComponentSize, ElMessageBox, TabsPaneContext } from 'element-plus'

const formData = ref({
  show: false,
  deleteloading: false,
  loading: false,
  activeName: 'first',
  hideErrBtn: false,
  showFooter: true,
  title: '',
  second: {
    hostname: '',
    mac: '',
    ip: '',
  },
  client: {
    hostname: '',
    ip: '',
    mac: '',
    nick: {
      isPush: false,
      workType: {
        onWorkTime: '',
        offWorkTime: '',
        webhookUrl: '',
        isSaturdayWork: false,
      } as WorkType,
    } as NickEntry,
  } as Client,
})
const handleShowSelect = (row: DayData) => {
  row.showSelect = !row.showSelect
}
const handleSelectChange = (row: DayData) => {
  row.showSelect = false
  // showWarmDialog(`${JSON.stringify(row)}`, {}, {})
}

//activities.length > 0
function isThridShow(): boolean {
  if (
    formData.value &&
    formData.value.client &&
    formData.value.client.nick &&
    formData.value.client.nick.workType &&
    formData.value.client.nick.workType.onWorkTime != '' &&
    formData.value.client.nick.workType.offWorkTime != ''
  ) {
    return true
  }
  return false
}

const getTagType = (value: number) => {
  switch (value) {
    case 0:
      return 'success'
    case 1:
      return 'danger'
    case 2:
      return 'warning'
    case 3:
      return 'warning'
    default:
      return 'primary'
  }
}

const getTagName = (value: number) => {
  switch (value) {
    case 0:
      return ''
    case 1:
      return ''
    case 2:
      return ''
    case 3:
      return ''
    default:
      return ''
  }
}
const options = [
  {
    value: 0,
    label: '',
  },
  {
    value: 1,
    label: '',
  },
  {
    value: 2,
    label: '',
  },
  {
    value: 3,
    label: '',
  },
]
const value3 = ref<number>(0)
const width = ref<string>('30%')
const monthData = ref<MonthData[]>([])
const currentPage = ref<number>(1)
const pageSize = ref<number>(10)
const size = ref<ComponentSize>('default')
// 
const paginatedTableData = computed<MonthData[]>(() => {
  const start = (currentPage.value - 1) * pageSize.value
  const end = start + pageSize.value
  return monthData.value.slice(start, end)
})
//  
const handlePageChange = (page: number) => {
  currentPage.value = page
}
const handleSizeChange = (val: number) => {
  console.log(`${val} items per page`)
  pageSize.value = val
}

const handleClick = (tab: TabsPaneContext) => {
  formData.value.hideErrBtn = false
  formData.value.showFooter = true
  console.log('handleClick', tab.paneName)
  width.value = '30%'
  switch (tab.paneName) {
    case 'first':
      break
    case 'second':
      formData.value.hideErrBtn = true
      break
    case 'thrid':
      formData.value.showFooter = false
      width.value = '50%'
      break
  }
}

const handleChangeWorkTime = (row: DayData) => {
  const loadings = showLoading('...')
  const body = {
    mac: formData.value.client.mac,
    day: row.date,
    data: row,
  }
  console.log('handleChangeWorkTime', body)
  fetch('../api/work/update', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(body),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('handleChangeWorkTime', json)
      showTips(json.code, json.msg)
    })
    .catch((error) => {
      console.log('error', error)
      showErrorTips('')
    })
    .finally(() => {
      loadings.close()
    })
}

const handleDeleteWorkTime = (row: DayData) => {
  const loadings = showLoading('...')
  const body = {
    mac: formData.value.client.mac,
    day: row.date,
  }
  console.log('handleChangeWorkTime', body)
  fetch('../api/work/del', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(body),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('handleChangeWorkTime', json)
      showTips(json.code, json.msg)
    })
    .catch((error) => {
      console.log('error', error)
      showErrorTips('')
    })
    .finally(() => {
      loadings.close()
    })
}
// 
// const defaultExpandedKeys = ref(['1001'])
//
// // /
// const handleExpandChange = (row: any, expanded: any) => {
//   console.log(':', row, ':', expanded)
//   // /
//   if (expanded) {
//     // 
//     // loadChildrenData(row.id)
//   }
// }

function initOnWorkTime() {
  formData.value.client = {} as Client
  formData.value.client.nick = {} as NickEntry
  formData.value.client.nick.workType = {
    onWorkTime: '',
    offWorkTime: '',
    webhookUrl: '',
    isSaturdayWork: false,
  } as WorkType
}

function checkOnWorkTime() {
  if (!formData.value.client) {
    formData.value.client = {} as Client
  }
  if (!formData.value.client.nick) {
    formData.value.client.nick = {} as NickEntry
  }
  if (!formData.value.client.nick.workType) {
    formData.value.client.nick.workType = {
      onWorkTime: '',
      offWorkTime: '',
      webhookUrl: '',
      isSaturdayWork: false,
    } as WorkType
  }
}

function handleAddWorkTime(isOnWork: boolean) {
  console.log('handleAddWorkTime', value3.value, formData.value.client.mac)
  const loadings = showLoading('...')
  const row = {
    timestamp: value3.value,
    mac: formData.value.client.mac,
    isOnWork: isOnWork,
  }
  fetch('../api/work/add', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(row),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('handleAddWorkTime', json)
      showTips(json.code, json.msg)
    })
    .catch((error) => {
      console.log('error', error)
      showErrorTips('')
    })
    .finally(() => {
      loadings.close()
    })
}

function fetchWorkEvent() {
  const row = {
    mac: formData.value.client.mac,
  }
  fetch('../api/work/tigger', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(row),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('fetchWorkEvent', json)
      showTips(json.code, json.msg)
    })
    .catch((error) => {
      console.log('error', error)
    })
    .finally(() => {})
}

function fetchWorkData() {
  const row = {
    mac: formData.value.client.mac,
  }
  fetch('../api/work/get', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(row),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('fetchWorkData', json)
      if (json.code === 0 && json.data) {
        monthData.value = json.data
      } else {
        //showTips(json.code, json.msg)
      }
    })
    .catch((error) => {
      console.log('error', error)
    })
    .finally(() => {})
}

const handleNickSetting = () => {
  const row = {
    name: formData.value.client.nick.name,
    isPush: formData.value.client.nick.isPush,
    starTime: formData.value.client.starTime,
    mac: formData.value.client.mac,
    ip: formData.value.client.ip,
    hostname: formData.value.client.hostname,
    workType: formData.value.client.nick.workType,
  }
  console.log('handleChangeNickName', row)
  formData.value.loading = true
  fetch('../api/nick/set', {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(row),
  })
    .then((res) => {
      return res.json()
    })
    .then((json) => {
      console.log('handleChangeNickName', json)
      showTips(json.code, json.msg)
    })
    .catch((error) => {
      console.log('error', error)
      showErrorTips('')
    })
    .finally(() => {
      formData.value.loading = false
      setTimeout(function () {
        hideDialog()
      }, 300)
    })
}

const handleDeleteStaticIp = () => {
  console.log('handleDeleteStaticIp', formData.value.client.mac)
  const mac = formData.value.client.mac
  const name = formData.value.client.hostname
  formData.value.deleteloading = true
  ElMessageBox.confirm(`${name}IP?`, 'Warning', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning',
  })
    .then(() => {
      const loader = showLoading('...')
      fetch(`../api/staticip/delete?mac=${mac}`, {
        credentials: 'include',
        method: 'DELETE',
      })
        .then((res) => {
          return res.json()
        })
        .then((json) => {
          console.log('handleDeleteStaticIp', json)
          showTips(json.code, json.msg)
        })
        .catch((error) => {
          console.log('error', error)
          showErrorTips('')
        })
        .finally(() => {
          loader.close()
          formData.value.deleteloading = false
          hideDialog()
        })
    })
    .catch(() => {})
}

function handleConfirm() {
  console.log('handleConfirm')
  switch (formData.value.activeName) {
    case 'first':
      handleNickSetting()
      break
    case 'second':
      handleStaticSet()
      break
  }
}

function handleStaticSet() {
  formData.value.loading = true
  const body = {
    hostname: formData.value.second.hostname,
    ip: formData.value.second.ip,
    mac: formData.value.second.mac,
  }
  console.log('handleStaticSet', body)
  fetch(`../api/staticip/set`, {
    credentials: 'include',
    method: 'POST',
    body: JSON.stringify(body),
  })
    .then((res) => res.json())
    .then((json) => {
      console.log('static ip setting', json)
      if (json && json.code === 0) {
        showSucessTips(json.msg)
      } else {
        showTips(json.code, json.msg)
      }
    })
    .catch((error) => {
      console.log(error)
      showErrorTips(`${JSON.stringify(error)}`)
    })
    .finally(() => {
      formData.value.loading = false
      setTimeout(function () {
        hideDialog()
      }, 300)
    })
}

const showDialogForm = (row: Client) => {
  console.log('row:', row)
  initOnWorkTime()
  formData.value.title = ``
  formData.value.client = row
  formData.value.show = true

  formData.value.second.hostname = row.hostname
  formData.value.second.ip = row.ip
  formData.value.second.mac = row.mac
  if (row && row.static) {
    formData.value.second.hostname = row.static.hostname
    formData.value.second.ip = row.static.ip
    formData.value.second.mac = row.static.mac
  }
  // activities.value = testSettingData
  checkOnWorkTime()
  fetchWorkData()
}

function hideDialog() {
  formData.value.show = false
}

// 
defineExpose({
  showDialogForm: showDialogForm,
})
</script>
<style scoped>
.upgrade-popup-header h3 {
  line-height: 2.5;
  margin: 0;
}

.upgrade-popup-content {
  height: auto;
  padding: 20px;
}

.upgrade-popup-footer button {
  margin-left: 10px;
}

@media screen and (max-width: 1180px) {
  .main-width {
    width: 30%;
  }
}

@media screen and (max-width: 968px) {
  .main-width {
    width: 80%;
  }
}

/*  +  */
.dialog-wrapper :deep(.el-dialog__header) {
  display: none;
}
</style>
