import { ElLoading, ElMessage, ElMessageBox } from 'element-plus'

export function showWarmDialog(title: string, ok: any, cancel: any) {
  ElMessageBox.confirm(title, 'Warning', {
    confirmButtonText: '',
    cancelButtonText: '',
    type: 'warning',
  })
    .then(() => {
      ok()
    })
    .catch(() => {
      cancel()
    })
}

export function showErrorTips(message: string) {
  ElMessage({
    showClose: true,
    message: message,
    type: 'error',
  })
  // ElMessage.error(message)
  console.log(message)
}

export function showTips(code: any, message: string) {
  if (code === 0) {
    showSucessTips(message)
  } else {
    showWarmTips(message)
  }
}

export function showSucessTips(message: string) {
  ElMessage({
    showClose: true,
    message: message,
    type: 'success',
  })
}

export function showWarmTips(message: string) {
  ElMessage({
    showClose: true,
    message: message,
    type: 'warning',
  })
}

/**
 *  Promise  XMLHttpRequest 
 * @param {Object} config - 
 * @param {string} config.url - 
 * @param {string} [config.method='GET'] - 
 * @param {Object} [config.headers] - 
 * @param {any} [config.data] - 
 * @param {number} [config.timeout=0] - 
 * @param {string} [config.responseType] - 
 * @param {Function} [config.onUploadProgress] - 
 * @param {Function} [config.onDownloadProgress] - 
 * @returns {Promise}  Promise 
 */
export function xhrPromise(config: any) {
  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    // 
    xhr.open(config.method || 'GET', config.url)
    // 
    if (config.headers) {
      Object.entries(config.headers).forEach(([key, value]) => {
        xhr.setRequestHeader(key, value as string)
      })
    }

    // 
    if (config.responseType) {
      xhr.responseType = config.responseType
    }

    // 
    if (config.timeout) {
      xhr.timeout = config.timeout
    }

    // 
    if (config.onUploadProgress) {
      xhr.upload.onprogress = (event) => {
        if (event.lengthComputable) {
          const percentComplete = (event.loaded / event.total) * 100
          console.log('--->', percentComplete + '%')
          config.onUploadProgress(percentComplete.toFixed(2))
        }
      }
    }

    // 
    if (config.onDownloadProgress) {
      xhr.onprogress = (e) => {
        config.onDownloadProgress({
          loaded: e.loaded,
          total: e.total,
          progress: e.loaded / e.total,
        })
      }
    }

    // 
    xhr.onload = () => {
      if (xhr.status >= 200 && xhr.status < 300) {
        resolve({
          data: xhr.response,
          status: xhr.status,
          statusText: xhr.statusText,
          headers: xhr.getAllResponseHeaders(),
        })
      } else {
        reject(new Error(`${xhr.status} ${xhr.statusText}`))
      }
    }

    // 
    xhr.onerror = () => reject(new Error(''))
    xhr.ontimeout = () => reject(new Error(`${config.timeout}ms`))
    xhr.onabort = () => reject(new Error(''))

    // 
    try {
      xhr.send(config.data)
    } catch (err) {
      reject(err)
    }
  })
}

export function syntaxHighlight(json: string): string {
  //  XSS
  json = json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;')

  //  JSON 
  return json.replace(
    /("(\\u[\dA-Fa-f]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+-]?\d+)?)/g,
    (match) => {
      let cls = 'number'
      if (/^"/.test(match)) {
        cls = match.endsWith(':') ? 'key' : 'string' // 
      } else if (/true|false/.test(match)) {
        cls = 'boolean'
      } else if (/null/.test(match)) {
        cls = 'null'
      }
      return `<span class="${cls}">${match}</span>` // [1,6](@ref)
    },
  )
}

export function isMobile() {
  if (isMobileWidth() || isMobilePhone()) {
    return true
  }
  return false
}

export function isMobileWidth() {
  if (window.innerWidth >= 992) {
    return false
  }
  return true
}

export function isMobilePhone() {
  const ua = navigator.userAgent.toLowerCase()
  return !!ua.match(/iOS|iPhone|Android|windows Phone|BB\d+/i)
}

export function showLoading(title: string) {
  return ElLoading.service({
    lock: true,
    text: title,
    background: 'rgba(0, 0, 0, 0.7)',
  })
}

export function markdownToHtml(markdown: string): string {
  let lines: string[] = markdown.split('\n')
  let html: string = ''
  let inList: boolean = false
  let listItems: string[] = []
  let inCodeBlock: boolean = false
  let codeBlockContent: string = ''

  for (let i = 0; i < lines.length; i++) {
    let line: string = lines[i].trim()

    // 
    if (line.startsWith('```')) {
      if (inCodeBlock) {
        html += `<pre><code>${codeBlockContent}</code></pre>`
        inCodeBlock = false
        codeBlockContent = ''
      } else {
        inCodeBlock = true
      }
      continue
    }

    if (inCodeBlock) {
      codeBlockContent += line + '\n'
      continue
    }

    // 
    if (/^(#+) (.*)$/.test(line)) {
      let [, hashes, content] = line.match(/^(#+) (.*)$/)!
      let level: number = hashes.length
      if (inList) {
        html += `<ul>${listItems.join('')}</ul>`
        inList = false
        listItems = []
      }
      html += `<h${level}>${content}</h${level}>`
    }
    // 
    else if (/^([*-]) (.*)$/.test(line)) {
      let [, , content] = line.match(/^([*-]) (.*)$/)!
      if (!inList) {
        inList = true
      }
      listItems.push(`<li>${content}</li>`)
    }
    // 
    else {
      if (inList) {
        html += `<ul>${listItems.join('')}</ul>`
        inList = false
        listItems = []
      }
      if (line) {
        html += `<p>${line}</p>`
      }
    }
  }

  // 
  if (inList) {
    html += `<ul>${listItems.join('')}</ul>`
  }

  // 
  if (inCodeBlock) {
    html += `<pre><code>${codeBlockContent}</code></pre>`
  }

  // 
  html = html.replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')

  // 
  html = html.replace(/\*(.*?)\*/g, '<em>$1</em>')

  return html
}

