export function updateOrAdd<T>(arr: T[], target: T, newValue: T): T[] {
  const index = arr.indexOf(target) // 
  if (index !== -1) {
    arr[index] = newValue // 
  } else {
    arr.push(newValue) // 
  }
  return arr
}

/**
 * 
 * @param nanoseconds 
 * @param decimalPlaces 2
 * @returns 
 */
export function formatNanosecondsToHours(
  nanoseconds: number,
  decimalPlaces: number = 2,
): string {
  const hours = nanoseconds / 3600000000000
  return hours.toFixed(decimalPlaces)
}
