const handleUploadUpgradeBin = (options: any) => {
  const { file } = options
  const formData = new FormData()
  formData.append('file', file)
  const loading = showLoading('...')
  globalProgress.value = 0
  manusForm.value.show = false
  xhrPromise({
    url: '../api/upgrade',
    method: 'POST',
    data: formData,
    onUploadProgress: (progress: string) => {
      console.log(`${progress}`)
      loading.setText(`...${progress}%`)
      globalProgress.value = parseInt(progress)
    },
  })
    .then((data: any) => {
      console.log('', data)
      // 
      const json = JSON.parse(data.data)
      if (json.code !== 0) {
        if (json.msg !== '') {
          showErrorTips(json.msg)
        }
      } else {
        if (json.msg !== '') {
          showSucessTips(json.msg)
        }
      }
    })
    .catch((error) => {
      console.error('', error)
      // 
      //showErrorTips('')
    })
    .finally(() => {
      setTimeout(function () {
        loading.close()
        globalProgress.value = 0
        manusForm.value.show = false
        window.location.reload()
      }, 4000)
    })
}

const handleUpdate = () => {
  if (manusForm.value.binUrl.length > 0) {
    const binUrl = manusForm.value.binUrl
    console.log('upgradeByUrl', binUrl)
    const loading = showLoading('...')
    manusForm.value.show = false
    fetch('../api/upgrade', {
      credentials: 'include',
      method: 'PUT',
      body: binUrl,
    })
      .then((res) => {
        return res.json()
      })
      .then((json) => {
        showTips(json.code, json.msg)
      })
      .catch(() => {
        showWarmTips('')
      })
      .finally(() => {
        setTimeout(function () {
          loading.close()
          window.location.reload()
        }, 4000)
      })
  } else {
    showWarmTips('url')
  }
}

